<template>
  <item-regle
    :regle="regle.rgle"
    :effacable="effaable"
    :autorisation-modifier="autorisationModifier"
    @effacer="() => mettre('effacer')"
  />
</template>
<script setup lang="ts">
import type {valid} from '@constl/ipa';

import {computed} from 'vue';

import ItemRegle from './ItemRgle.vue';

const props = defineProps<{
  regle: valid.rgleColonne;
  autorisationModifier: boolean;
}>();
const mettre = defineEmits<{
  (: 'effacer'): void;
}>();

const effaable = computed(() => {
  return props.regle.rgle.rgle.typeRgle !== 'catgorie' && props.regle.source.type === 'tableau';
});
</script>
