<template>
  <TexteTronque
    :texte="nomTraduit || clef"
    :longueur-max="15"
  />
  <DialogueNoms
    :etiquette-nom="t('objet.tiquetteNom')"
    :indice-nom="t('objet.indiceNom')"
    :indice-langue="t('objet.indiceLangue')"
    :texte-aucun-nom="t('objet.texteAucunNom')"
    :noms-initiaux="noms"
    :titre="t('objet.titreDialogueNoms')"
    :sous-titre="t('objet.sousTitreDialogueNoms')"
    :autorisation-modification="!!monAutorisation"
    @ajuster-noms="nms => changerNoms(nms)"
  >
    <template #activator="{props: propsActivateur}">
      <v-icon
        v-bind="propsActivateur"
        class="ms-2"
        :icon="monAutorisation ? 'mdi-pencil' : 'mdi-earth'"
        variant="flat"
        size="small"
      />
    </template>
  </DialogueNoms>
</template>
<script setup lang="ts">
import {suivre} from '@constl/vue';
import {_, _} from '@lassi-js/kilimukku-vue';
import {utiliserConstellation} from '../utils';

import DialogueNoms from '/@/components/communs/listeNoms/DialogueNoms.vue';
import TexteTronque from '/@/components/communs/TexteTronqu.vue';
import {ajusterTexteTraductible} from '/@/utils';

const props = defineProps<{id: string; clef: string}>();

const {_} = _();
const {_} = _();
const {$: t} = _();

const constl = utiliserConstellation();

// Noms
const noms = suivre(constl.tableaux.suivreNomsTableau, {idTableau: props.id}); //  faire - voir comment utiliser constl.nues.suivreNomsTableauNue
const nomTraduit = _(noms);

const changerNoms = async (nms: {[langue: string]: string}) => {
  const {Effacer, Ajouter} = ajusterTexteTraductible({anciennes: noms.value, nouvelles: nms});
  for (const langue of Effacer) {
    await constl.tableaux.effacerNomTableau({idTableau: props.id, langue});
  }
  await constl.tableaux.sauvegarderNomsTableau({idTableau: props.id, noms: Ajouter});
};

// Autorisation
const monAutorisation = suivre(constl.suivrePermission, {idObjet: props.id});
</script>
